@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Copal Std';
  src:  url(/static/media/copal-std-decorated.473d603c.woff) format('woff');
}

:root {
  --color-01: rgb(240, 255, 255);
  --color-02: #88ABFB;
  --color-03: rgb(30,30,30);
  --color-04: #F5F5F5;
}

.light {
  color-scheme: light;
  --background-primary: var(--color-04);
  --background-secondary: var(--color-01);
  --cta-primary: var(--color-02);
  --text-primary: var(--color-03);
  --text-secondary: var(--color-03);
}

.dark {
  color-scheme: dark;
  --background-primary: var(--color-03);
  --background-secondary: var(--color-01);
  --cta-primary: var(--color-02);
  --text-primary: var(--color-01);
  --text-secondary: var(--color-03)
}

.App {
  font-family: 'Noto Sans TC', sans-serif;
  min-height: 100vh;
  text-align: center;
  background-color: var(--background-primary);
  color: var(--text-primary);
}
.logo {
  color: var(--text-primary);
  margin-top: 0;
  padding-top: 1em;
  font-family: 'Copal Std', sans-serif;
  font-size: 64px;
} 

.show_random_card_btn {
  font-family: 'Noto Sans TC', sans-serif;
  cursor: pointer;
  font-weight: 500;
  width: 200px;
  font-size: 24px;
  margin-top: 20px;
  border: 0;
  background-color: var(--cta-primary);
}

.option-header {
  font-weight: 500;
}

.option-list {
  border: 2px solid var(--text-primary);
}

.switch-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  height: 34px;
  padding: 30px;
}
.theme-icon--dark {
  height: 34px;
  /* font-size: 14pt; */
  line-height: 2;
  padding-right: 5px;
}

.theme-icon--light {
  height: 34px;
  /* font-size: 14pt; */
  line-height: 2;
  padding-right: 5px;
}

/* Toggle */
/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--cta-primary);
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: var(--background-secondary);
  transition: .4s;
}

input:checked + .slider {
  background-color: var(--cta-primary);
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--cta-primary);
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

@media only screen and (max-width: 768px){
  .logo {
    font-size: 48px;
  }
}
.card {
  box-sizing: border-box;
  position: relative;
  margin:0 auto;
  width: 500px;
  /* min-width: 100%;
  max-width: 500px; */
  height: 300px;
}

.card__face {
  position: absolute;
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  font-size:48pt;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  color: var(--text-secondary);
  background-color: var(--background-secondary);
  border: 2px solid var(--text-primary);

}

@media only screen and (max-width: 768px){
  .card__face {
    font-size: 32pt;
  }
  .card {
    width: 100%;
  }
}
